<template>
  <transition name="fade">
    <div class="register" v-show="logOutState">
      <div class="wrap">
        <div class="icon-" @click="outLogout()"></div>
        <div class="title"></div>
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px"
                 class="demo-ruleForm">
          <el-form-item label="" prop="user">
            <el-input type="text" v-model="ruleForm2.user" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="pass">
            <el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checkPass">
            <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="ruleForm2.sex" label=""></el-radio>
            <el-radio v-model="ruleForm2.sex" label=""></el-radio>
          </el-form-item>
          <el-form-item
            prop="email"
            label=""
            :rules="[
      {  message: '', trigger: 'blur' },
      { type: 'email', message: '', trigger: 'blur,change' }
    ]"
          >
            <el-input v-model="ruleForm2.email"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
            <el-button @click="resetForm('ruleForm2')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </transition>
</template>

<script>
  export default {
    data() {
      var checkUser = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''))
        }
        setTimeout(() => {
          if (value.length < 4) {
            return callback(new Error('4'))
          } else {
            return callback()
          }
        }, 1000);
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      }
      return {
        ruleForm2: {
          pass: '',
          checkPass: '',
          user: '',
          email: '',
          sex: ''
        },
        rules2: {
          pass: [
            {
              validator: validatePass, trigger: 'blur'
            }
          ],
          checkPass: [
            {
              validator: validatePass2, trigger: 'blur'
            }
          ],
          user: [
            {
              validator: checkUser, trigger: 'blur'
            }
          ]
        }
      };
    },
    computed: {
      logOutState() {
        return this.$store.getters.getLogoutState
      }
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
//            console.log(this.ruleForm2)
            this.$http.post('/user/sendUser', this.ruleForm2).then(res => {
              if (res.status == 200) {
                if (res.data.code === 1) {
                  this.$message.warning(res.data.meg);
                  this.$refs[formName].resetFields();
                } else {
                  this.$message.warning(res.data.meg);
                  this.$refs[formName].resetFields();
                }
              } else {
                this.$message.warning('');
              }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      outLogout() {
        this.$store.dispatch('setLogoutMiss', false)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @keyframes rotate-close
      0%
        transform: rotate(0)
      100%
        transform: rotate(360deg)
    .register
      position: absolute;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100vh;
      background-color: rgba(7, 17, 27, 0.8);
      blur: 10px;
      .icon-
        cursor: pointer;
        z-index: 120;
        position: absolute;
        right: 50px;
        top: 50px;
        font-size: 32px;
        color: #fff;
      .icon-:hover
        animation: rotate-close .5s ease-in-out;
      .title
        font-size: 32px;
        color: #fff;
        text-align: center;
        margin-bottom: 30px;
        padding-top: 30px;
      .wrap
        width: 80%;
        height: auto;
        margin: 0 auto;
        label
          color: #fff;
        button
          margin-right: 10px;
          float: right;
    .fade-enter-active, .fade-leave-active
      transition: opacity 1s;
    .fade-enter, .fade-leave-to
      opacity: 0;
</style>



